'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { Progress } from '@/components/ui/progress'
import { Separator } from '@/components/ui/separator'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { Collapsible, CollapsibleContent, CollapsibleTrigger } from '@/components/ui/collapsible'
import { Textarea } from '@/components/ui/textarea'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import Link from 'next/link'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import { z } from 'zod'

const campaignSchema = z.object({
  name: z.string().min(1, 'Campaign name is required'),
  message: z.string().min(1, 'Message is required'),
  selectedContacts: z.array(z.string()).min(1, 'At least one contact must be selected'),
})

type CampaignForm = z.infer<typeof campaignSchema>

interface Contact {
  id: string
  name?: string
  e164: string
  status: string
  optInAt?: string
}

interface Campaign {
  id: string
  name: string
  status: string
  totalContacts: number
  sentMessages: number
  deliveredMessages: number
  readMessages: number
  createdAt: string
}

interface BatchCampaign {
  id: string
  name: string
  status: string
  totalContacts: number
  numSubCampaigns: number
  campaigns: Campaign[]
  createdAt: string
}

// Mock data for demonstration
const mockContacts: Contact[] = [
  { id: '1', name: 'John Doe', e164: '+1234567890', status: 'ACTIVE', optInAt: '2024-01-15' },
  { id: '2', name: 'Jane Smith', e164: '+1234567891', status: 'ACTIVE', optInAt: '2024-01-16' },
  { id: '3', name: 'Bob Johnson', e164: '+1234567892', status: 'ACTIVE', optInAt: '2024-01-17' },
]

const mockBatchCampaigns: BatchCampaign[] = [
  {
    id: '1',
    name: 'Holiday Promotion 2024',
    status: 'COMPLETED',
    totalContacts: 1200,
    numSubCampaigns: 3,
    createdAt: '2024-01-15T10:00:00Z',
    campaigns: [
      {
        id: '1-1',
        name: 'Holiday Promotion 2024 - Batch 1',
        status: 'COMPLETED',
        totalContacts: 400,
        sentMessages: 400,
        deliveredMessages: 395,
        readMessages: 380,
        createdAt: '2024-01-15T10:00:00Z',
      },
      {
        id: '1-2',
        name: 'Holiday Promotion 2024 - Batch 2',
        status: 'COMPLETED',
        totalContacts: 400,
        sentMessages: 400,
        deliveredMessages: 398,
        readMessages: 385,
        createdAt: '2024-01-15T10:05:00Z',
      },
      {
        id: '1-3',
        name: 'Holiday Promotion 2024 - Batch 3',
        status: 'COMPLETED',
        totalContacts: 400,
        sentMessages: 400,
        deliveredMessages: 392,
        readMessages: 375,
        createdAt: '2024-01-15T10:10:00Z',
      },
    ],
  },
  {
    id: '2',
    name: 'Product Launch Announcement',
    status: 'RUNNING',
    totalContacts: 800,
    numSubCampaigns: 2,
    createdAt: '2024-01-20T14:00:00Z',
    campaigns: [
      {
        id: '2-1',
        name: 'Product Launch Announcement - Batch 1',
        status: 'COMPLETED',
        totalContacts: 400,
        sentMessages: 400,
        deliveredMessages: 385,
        readMessages: 320,
        createdAt: '2024-01-20T14:00:00Z',
      },
      {
        id: '2-2',
        name: 'Product Launch Announcement - Batch 2',
        status: 'RUNNING',
        totalContacts: 400,
        sentMessages: 250,
        deliveredMessages: 240,
        readMessages: 180,
        createdAt: '2024-01-20T14:05:00Z',
      },
    ],
  },
]

export default function CampaignsPage() {
  const [selectedContacts, setSelectedContacts] = useState<string[]>([])
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false)
  const [expandedBatchCampaigns, setExpandedBatchCampaigns] = useState<string[]>([])

  const {
    register,
    handleSubmit,
    formState: { errors, isSubmitting },
    setValue,
    reset,
  } = useForm<CampaignForm>({
    resolver: zodResolver(campaignSchema),
  })

  const toggleContactSelection = (contactId: string) => {
    const newSelection = selectedContacts.includes(contactId)
      ? selectedContacts.filter(id => id !== contactId)
      : [...selectedContacts, contactId]
    
    setSelectedContacts(newSelection)
    setValue('selectedContacts', newSelection)
  }

  const toggleBatchCampaignExpansion = (batchId: string) => {
    setExpandedBatchCampaigns(prev =>
      prev.includes(batchId)
        ? prev.filter(id => id !== batchId)
        : [...prev, batchId]
    )
  }

  const onSubmit = async (data: CampaignForm) => {
    try {
      const response = await fetch('/api/campaigns/batch', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: data.name,
          message: data.message,
          contactIds: data.selectedContacts,
        }),
      })

      const result = await response.json()

      if (response.ok && result.success) {
        setIsCreateDialogOpen(false)
        reset()
        setSelectedContacts([])
        // Refresh campaigns list
      } else {
        console.error('Campaign creation error:', result.error)
      }
    } catch (error) {
      console.error('Campaign creation error:', error)
    }
  }

  const getStatusBadge = (status: string) => {
    const statusConfig = {
      DRAFT: { variant: 'secondary' as const, label: 'Draft' },
      SCHEDULED: { variant: 'default' as const, label: 'Scheduled' },
      RUNNING: { variant: 'default' as const, label: 'Running' },
      PAUSED: { variant: 'secondary' as const, label: 'Paused' },
      COMPLETED: { variant: 'secondary' as const, label: 'Completed' },
      FAILED: { variant: 'destructive' as const, label: 'Failed' },
    }

    const config = statusConfig[status as keyof typeof statusConfig] || statusConfig.DRAFT
    return <Badge variant={config.variant}>{config.label}</Badge>
  }

  const calculateProgress = (sent: number, total: number) => {
    return total > 0 ? (sent / total) * 100 : 0
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <Link href="/" className="text-2xl font-bold text-gray-900">
                WhatsApp Campaign Manager
              </Link>
            </div>
            <div className="flex items-center space-x-4">
              <Dialog open={isCreateDialogOpen} onOpenChange={setIsCreateDialogOpen}>
                <DialogTrigger asChild>
                  <Button>Create Campaign</Button>
                </DialogTrigger>
                <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
                  <DialogHeader>
                    <DialogTitle>Create New Campaign</DialogTitle>
                    <DialogDescription>
                      Create a WhatsApp marketing campaign. Large contact lists will be automatically split into batches of 400 contacts.
                    </DialogDescription>
                  </DialogHeader>

                  <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                    <div>
                      <Label htmlFor="name">Campaign Name</Label>
                      <Input
                        id="name"
                        placeholder="Enter campaign name"
                        {...register('name')}
                      />
                      {errors.name && (
                        <p className="text-sm text-red-600 mt-1">{errors.name.message}</p>
                      )}
                    </div>

                    <div>
                      <Label htmlFor="message">Message</Label>
                      <Textarea
                        id="message"
                        placeholder="Enter your message"
                        rows={4}
                        {...register('message')}
                      />
                      {errors.message && (
                        <p className="text-sm text-red-600 mt-1">{errors.message.message}</p>
                      )}
                    </div>

                    <div>
                      <Label>Select Contacts</Label>
                      <div className="mt-2 max-h-48 overflow-y-auto border rounded-md">
                        <Table>
                          <TableHeader>
                            <TableRow>
                              <TableHead className="w-12">Select</TableHead>
                              <TableHead>Name</TableHead>
                              <TableHead>Phone</TableHead>
                              <TableHead>Status</TableHead>
                            </TableRow>
                          </TableHeader>
                          <TableBody>
                            {mockContacts.map((contact) => (
                              <TableRow key={contact.id}>
                                <TableCell>
                                  <input
                                    type="checkbox"
                                    checked={selectedContacts.includes(contact.id)}
                                    onChange={() => toggleContactSelection(contact.id)}
                                    className="rounded"
                                  />
                                </TableCell>
                                <TableCell>{contact.name || 'N/A'}</TableCell>
                                <TableCell>{contact.e164}</TableCell>
                                <TableCell>
                                  <Badge variant="secondary">{contact.status}</Badge>
                                </TableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                        </Table>
                      </div>
                      {errors.selectedContacts && (
                        <p className="text-sm text-red-600 mt-1">{errors.selectedContacts.message}</p>
                      )}
                    </div>

                    {selectedContacts.length > 400 && (
                      <Alert>
                        <AlertDescription>
                          Your selection of {selectedContacts.length} contacts will be automatically split into {Math.ceil(selectedContacts.length / 400)} batch campaigns of 400 contacts each.
                        </AlertDescription>
                      </Alert>
                    )}

                    <div className="flex justify-end space-x-2">
                      <Button
                        type="button"
                        variant="outline"
                        onClick={() => setIsCreateDialogOpen(false)}
                      >
                        Cancel
                      </Button>
                      <Button type="submit" disabled={isSubmitting}>
                        {isSubmitting ? 'Creating...' : 'Create Campaign'}
                      </Button>
                    </div>
                  </form>
                </DialogContent>
              </Dialog>

              <Link href="/">
                <Button variant="ghost">Back to Dashboard</Button>
              </Link>
            </div>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Campaign Management</h1>
          <p className="text-lg text-gray-600">
            Manage your WhatsApp marketing campaigns and track their performance
          </p>
        </div>

        {/* Campaign Statistics */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total Campaigns</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{mockBatchCampaigns.length}</div>
              <p className="text-xs text-gray-600 mt-1">Batch campaigns</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Active Campaigns</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {mockBatchCampaigns.filter(c => c.status === 'RUNNING').length}
              </div>
              <p className="text-xs text-gray-600 mt-1">Currently running</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Total Contacts</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {mockBatchCampaigns.reduce((sum, c) => sum + c.totalContacts, 0)}
              </div>
              <p className="text-xs text-gray-600 mt-1">Across all campaigns</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-gray-600">Messages Sent</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {mockBatchCampaigns.reduce((sum, bc) => 
                  sum + bc.campaigns.reduce((cSum, c) => cSum + c.sentMessages, 0), 0
                )}
              </div>
              <p className="text-xs text-gray-600 mt-1">Total messages</p>
            </CardContent>
          </Card>
        </div>

        {/* Batch Campaigns List */}
        <Card>
          <CardHeader>
            <CardTitle>Batch Campaigns</CardTitle>
            <CardDescription>
              View and manage your WhatsApp marketing campaigns. Large campaigns are automatically split into batches of 400 contacts.
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {mockBatchCampaigns.map((batchCampaign) => (
                <div key={batchCampaign.id} className="border rounded-lg">
                  <Collapsible
                    open={expandedBatchCampaigns.includes(batchCampaign.id)}
                    onOpenChange={() => toggleBatchCampaignExpansion(batchCampaign.id)}
                  >
                    <CollapsibleTrigger asChild>
                      <div className="p-4 hover:bg-gray-50 cursor-pointer">
                        <div className="flex items-center justify-between">
                          <div className="flex items-center space-x-4">
                            <div>
                              <h3 className="font-semibold text-lg">{batchCampaign.name}</h3>
                              <p className="text-sm text-gray-600">
                                {batchCampaign.totalContacts} contacts • {batchCampaign.numSubCampaigns} sub-campaigns
                              </p>
                            </div>
                          </div>
                          <div className="flex items-center space-x-4">
                            {getStatusBadge(batchCampaign.status)}
                            <div className="text-sm text-gray-500">
                              {new Date(batchCampaign.createdAt).toLocaleDateString()}
                            </div>
                            <div className="text-2xl">
                              {expandedBatchCampaigns.includes(batchCampaign.id) ? '−' : '+'}
                            </div>
                          </div>
                        </div>
                      </div>
                    </CollapsibleTrigger>

                    <CollapsibleContent>
                      <div className="px-4 pb-4">
                        <Separator className="mb-4" />
                        <div className="space-y-3">
                          <h4 className="font-medium text-sm text-gray-700">Sub-Campaigns</h4>
                          {batchCampaign.campaigns.map((campaign) => (
                            <div key={campaign.id} className="bg-gray-50 p-3 rounded-md">
                              <div className="flex items-center justify-between mb-2">
                                <div>
                                  <h5 className="font-medium">{campaign.name}</h5>
                                  <p className="text-sm text-gray-600">
                                    {campaign.totalContacts} contacts
                                  </p>
                                </div>
                                {getStatusBadge(campaign.status)}
                              </div>

                              <div className="grid grid-cols-3 gap-4 text-sm">
                                <div>
                                  <p className="text-gray-600">Sent</p>
                                  <p className="font-medium">{campaign.sentMessages}/{campaign.totalContacts}</p>
                                </div>
                                <div>
                                  <p className="text-gray-600">Delivered</p>
                                  <p className="font-medium">{campaign.deliveredMessages}</p>
                                </div>
                                <div>
                                  <p className="text-gray-600">Read</p>
                                  <p className="font-medium">{campaign.readMessages}</p>
                                </div>
                              </div>

                              <div className="mt-2">
                                <div className="flex justify-between text-xs text-gray-600 mb-1">
                                  <span>Progress</span>
                                  <span>{Math.round(calculateProgress(campaign.sentMessages, campaign.totalContacts))}%</span>
                                </div>
                                <Progress 
                                  value={calculateProgress(campaign.sentMessages, campaign.totalContacts)} 
                                  className="h-2"
                                />
                              </div>
                            </div>
                          ))}
                        </div>
                      </div>
                    </CollapsibleContent>
                  </Collapsible>
                </div>
              ))}

              {mockBatchCampaigns.length === 0 && (
                <div className="text-center py-8">
                  <div className="text-4xl mb-4">📱</div>
                  <h3 className="text-lg font-medium text-gray-900 mb-2">No campaigns yet</h3>
                  <p className="text-gray-600 mb-4">
                    Create your first WhatsApp marketing campaign to get started
                  </p>
                  <Button onClick={() => setIsCreateDialogOpen(true)}>
                    Create Your First Campaign
                  </Button>
                </div>
              )}
            </div>
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
